function sliderMain() {
    const swiperContainer = document.querySelector('.main__mob2');
    const nextSection = document.querySelector('.next-section');
    const swiper = new Swiper(".main__mob2 .mySwiper", {
        direction: "vertical",
        slidesPerView: 1,
        spaceBetween: 30,
        mousewheel: false, // Отключаем встроенный mousewheel, будем обрабатывать сами
        touchStartPreventDefault: false,
        simulateTouch: true,
    });

    // Флаг блокировки обработки wheel, чтобы избежать слишком частых переключений
    let wheelTimeout = null;

    swiperContainer.addEventListener('wheel', (e) => {
        // Если уже в процессе переключения слайда — игнорируем новые события
        if (wheelTimeout) {
            e.preventDefault();
            return;
        }

        const deltaY = e.deltaY;

        if (deltaY > 0) { // Скролл вниз
            if (swiper.isEnd) {
                // Если на последнем слайде — позволяем прокрутку страницы вниз
                // Но если есть следующая секция, плавно скроллим к ней
                if (nextSection) {
                    nextSection.scrollIntoView({ behavior: 'smooth' });
                    e.preventDefault(); // Предотвращаем дефолт, чтобы не дергался скролл
                } else {
                    // Если следующей секции нет — разрешаем прокрутку страницы
                    // Не preventDefault()
                }
            } else {
                // Если не на последнем слайде — листаем слайдер вниз и блокируем прокрутку страницы
                swiper.slideNext();
                e.preventDefault();

                // Блокируем обработку wheel на 700мс, чтобы не было множественных срабатываний
                wheelTimeout = setTimeout(() => {
                    wheelTimeout = null;
                }, 700);
            }
        } else if (deltaY < 0) { // Скролл вверх
            if (swiper.isBeginning) {
                // Если на первом слайде — разрешаем прокрутку страницы вверх (не preventDefault)
            } else {
                // Если не на первом слайде — листаем слайдер вверх и блокируем прокрутку страницы
                swiper.slidePrev();
                e.preventDefault();

                // Блокируем обработку wheel на 700мс
                wheelTimeout = setTimeout(() => {
                    wheelTimeout = null;
                }, 700);
            }
        }
    }, { passive: false });

    // Обработка тач-событий для мобильных — оставим как есть
    let startY = 0;
    let isScrollingTouch = false;

    swiperContainer.addEventListener('touchstart', (e) => {
        startY = e.touches[0].clientY;
        isScrollingTouch = false;
    }, { passive: true });

    swiperContainer.addEventListener('touchmove', (e) => {
        const currentY = e.touches[0].clientY;
        const diffY = startY - currentY;

        if (swiper.isEnd) {
            if (diffY > 0) {
                swiper.allowTouchMove = false;
                isScrollingTouch = true;
            } else {
                swiper.allowTouchMove = true;
            }
        } else {
            swiper.allowTouchMove = true;
        }
    }, { passive: true });

    swiperContainer.addEventListener('touchend', () => {
        if (isScrollingTouch) {
            swiper.allowTouchMove = false;
        } else {
            swiper.allowTouchMove = true;
        }
    });

    swiper.on('slideChange', () => {
        swiper.allowTouchMove = true;
    });
}

function sliderServices() {
    var swiper = new Swiper('.services .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 20,
        navigation: {
            nextEl: ".services .swiper-button-next",
            prevEl: ".services .swiper-button-prev",
        },
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            993: {
                spaceBetween: 20,
            },
        }
    });
}

function planGallery() {
    var swiper = new Swiper(".plan__images .mySwiper", {
        slidesPerView: 4,
        spaceBetween: 16,
        freeMode: true,
    });
    var swiper2 = new Swiper(".plan__images .mySwiper2", {
        spaceBetween: 16,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}

$(document).ready(function () {
    sliderServices();
    planGallery();
    sliderMain();

    // Menu
    $('.burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Header
    // let lastScrollTop = 0;
    // const header = $('#header');
    //
    // $(window).scroll(function() {
    //     let scrollTop = $(this).scrollTop();
    //
    //     if (scrollTop > lastScrollTop) {
    //         header.addClass('header-fixed');
    //     } else {
    //         header.removeClass('header-fixed');
    //     }
    //     lastScrollTop = scrollTop;
    // });

    // Btn-animation
    $(".btn-animation").mouseenter(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    $(".btn-animation").mousemove(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    // Form
    let isClicked = false;
    $('.form').on('submit', function(e) {
        e.preventDefault();

        isClicked = true;
        let allFilled = true;

        $(this).find('.form-group input').each(function() {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
                allFilled = false;
            } else {
                $formGroup.removeClass('form-group--active');
            }
        });
    });
    $('.form-group input').on('input', function() {
        if (isClicked) {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
            } else {
                $formGroup.removeClass('form-group--active');
            }
        }
    });

    // Up
    var t = $(".up");

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            t.addClass("up--active");
        } else {
            t.removeClass("up--active");
        }
    });

    t.on("click", function(e) {
        e.preventDefault();
        $("html, body").animate({
            scrollTop: 0
        }, 300);
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Plan
    $('.path-btn').on('mouseenter', function(e) {
        e.stopPropagation();

        var $btn = $(this);
        var id = $btn.data('id');

        var rect = this.getBoundingClientRect();
        var top = rect.top + $(window).scrollTop();
        var left = rect.left + $(window).scrollLeft();

        var $card = $('.service-card').filter('[data-id="' + id + '"]');
        var cardWidth = $card.outerWidth();
        var windowWidth = $(window).width();

        if (left + cardWidth > windowWidth + $(window).scrollLeft()) {
            left = windowWidth + $(window).scrollLeft() - cardWidth - 10;
        }

        $card.css({
            'top': (top - 80) + 'px',
            'left': left + 'px',
            'display': 'block'
        });

        if ($(window).width() <= 576) {
            $('.section-bg').addClass('section-bg--active')
            $card.css({
                'transform': 'translate(0)'
            });
        }
    });

    $('.path-btn, .service-card').on('mouseleave', function() {
        var $this = $(this);
        var id = $this.data('id');

        setTimeout(function() {
            if (!$('.path-btn[data-id="' + id + '"]:hover').length && !$('.service-card[data-id="' + id + '"]:hover').length) {
                $('.service-card[data-id="' + id + '"]').hide();
            }
        }, 100);
    });

    //
    $('.plan__btn').click(function () {
        $('.plan__images').addClass('plan__images--active')
    })

    $('.plan__images-close').click(function () {
        $('.plan__images').removeClass('plan__images--active')
    })

    //
    $('.section-bg').click(function () {
        $(this).removeClass('section-bg--active')
    })

    //
    if ($('.plan').length) {
        const $plan = $('.plan');
        const $planMob = $plan.find('.plan-mob');
        const $img2 = $planMob.find('.plan__img2');
        const $scrollBtn = $planMob.find('.scroll-btn');
        const $body = $('body');

        $body.addClass('fixed-body');

        let step = 0;
        let lastTouchY = null;
        const scrollThreshold = 25;
        let scrollTimeout;

        function hideImg2() {
            $img2.addClass('hidden');
        }

        function hidePlanMob() {
            $planMob.addClass('hidden');
            $body.removeClass('fixed-body');
            $(window).off('touchmove', onTouchMove);
            $(window).off('wheel', onWheel);
            $scrollBtn.off('click', onClick);
        }

        function handleScrollDown() {
            if (step === 0) {
                hideImg2();
                step = 1;
            } else if (step === 1) {
                clearTimeout(scrollTimeout);
                scrollTimeout = setTimeout(() => {
                    hidePlanMob();
                    step = 2;
                }, 300);
            }
        }

        function onTouchMove(e) {
            if (e.originalEvent.touches.length === 0) return;

            const touch = e.originalEvent.touches[0];
            const currentY = touch.clientY;

            if (lastTouchY === null) {
                lastTouchY = currentY;
                return;
            }

            const deltaY = lastTouchY - currentY;

            if (deltaY > scrollThreshold) {
                handleScrollDown();
                e.preventDefault();
            }

            lastTouchY = currentY;
        }

        function onWheel(e) {
            if (e.originalEvent.deltaY > scrollThreshold) {
                handleScrollDown();
                e.preventDefault();
            }
        }

        function onClick(e) {
            handleScrollDown();
            e.preventDefault();
        }

        $(window).on('touchmove', onTouchMove);
        $(window).on('wheel', onWheel);
        $scrollBtn.on('click', onClick);
    }
});




